.aboutus_img {
    width: 100%;
    max-width: 1440px;
    height: 180px;
}
.aboutus_banner{
    background-image: url(../images/About_us_1.png);
    width: 100%;
    background-size: 100%;
    background-repeat: no-repeat;
}
.aboutus_top .buttons {
    display: none;
}
.aboutus_top{
    background-image: url(../images/About_us_2.png);
    background-position: right bottom;
  background-repeat: no-repeat;
  background-size: 25%;
}

.aboutus_content {
    background-color: var(--green-700);
    background-image: url(../images/About_us_3.png);
    background-position: left top;
  background-repeat: no-repeat;
  background-size: 25%;
}


.aboutus_top .BigHeader,
.aboutus_top .BigHeader_text{
    padding-bottom: 40px;
}
.aboutus_content .BigHeader {
    color: var(--white);
    margin-bottom: 80px;
}

.aboutus_value{
    padding: 32px 0;
    width: 100%;
    max-width: 714px;
    text-align: left;
    border-top: solid 1px var(--white);
}
/* .aboutus_value:last-child{
    border-bottom: solid 1px var(--white);
} */
.aboutus_content .title {
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: -1px;
    color: var(--white);
    margin-bottom: 16px;
}

.aboutus_content .text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--white);
}


@media (min-width: 576px) {
    
    .aboutus_img {
        height: 324px;
    }

    .aboutus_content .title {
        font-size: 24px;
        line-height: 32px;
    }

    .aboutus_content .text {
        font-size: 20px;
        line-height: 24px;
    }
    .aboutus_top .BigHeader_container{
        max-width: 100%;
        margin-bottom: 100px;
   }
}

@media (min-width: 1024px) {
    .aboutus_banner{
        background-position: top 100px right;
        background-size: 540px;
    }
    .aboutus_top .BigHeader_container > div {
        display: flex;
        justify-content: space-between;
        column-gap: 100px;
        max-width: 100%;
    }

    .aboutus_img {
        height: 575px;
    }
    .aboutus_top{
        background-position: bottom right 0px;
        background-size: 420px;
    }
    .aboutus_top .BigHeader,
    .aboutus_top .BigHeader_text{
        padding-bottom: 0;
    }
    .aboutus_top .BigHeader_text{
padding-top: 32px;
    }
    /* .aboutus_top .split{
        disp
    } */
    .aboutus_content{
        background-position: top left -220px;
        background-size: 500px;
}
.aboutus_content .BigHeader {
    margin-bottom: 100px;
}
    .aboutus_value{
        display: grid;
        grid-template-columns: 264px auto;
        grid-column-gap: 40px;
        padding: 56px 0;
        margin-right: 0;
    margin-left: auto;
    }
    .aboutus_content .title {
       margin-bottom: 0;
    }
}

@media (min-width: 1280px) {
    .aboutus_banner{
        background-position: top 100px right;
    }
    .aboutus_top{
        background-position: bottom 20px right 0px;
        background-size: 500px;
    }
    .aboutus_img {
        max-width: 100%;
        height: fit-content;
    }

    .aboutus_value{
        max-width: 855px;
        
    }
    .aboutus_content .BigHeader_container{
        max-width: 100%;
   }
    .aboutus_content .title {
        font-size: 32px;
        line-height: 38px;
        width: 264px;
    }

    .aboutus_content .text {
        font-size: 24px;
        line-height: 32px;
    }
}